.container {
  position: relative;
}

.ScrollButton {
  position: fixed;
  right: 20px;
  bottom: 140px;
  width: 40px;
  height: 40px;

  border: none;
  background-color: inherit;
  background-image: url(../../../public/img/icon-up.svg);
  background-size: contain;

  transition: background-image 0.3s;

  &:hover {
    background-image: url(../../../public/img/icon-up-hover.svg);
  }
}