.Chat {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  height: 100vh;

  &__selected-container {
    overflow-y: auto;
    flex-grow: 1;
    padding: 10px 50px 10px 10px;
  }

  &__icon-back {
    margin-right: 25px;
  }

  &__header {
    display: flex;
    align-items: center;
    padding: 15px;
    background-color: #f5f5f5;
    border-bottom: 1px solid #d1cccc;;
    
    &-photo {
      margin-right: 25px;
    }

    &-name {
      font-size: 30px;
    }
  }
}